<template>
<li class="squad">
    <div class="icon squad"></div>
    <div :class="['status', squad.status]" v-if="showStatus">
        <div v-if="squad.status === 'mission'">
            <p>Mission</p>
            <span class="time">{{getSquadMissionTimeLeft(squad)}}</span>
        </div>
        <div v-else-if="squad.status === 'missionSuccess'" @click="missionResume()">
            <p>Mission accomplie</p>
        </div>
        <div v-else-if="squad.status === 'missionFail'" @click="missionResume()">
            <div class="icon fail"></div>
        </div>
        <div v-else-if="squad.status === 'dead'" @click="missionResume()">
            <div class="icon dead"></div>
        </div>
        <div v-else>
            <p>
            <router-link :to="'mission/'+indexSquad">Nouvelle mission</router-link>
            </p>
        </div>
    </div>
    <span class="name">Escouade {{getSquadName(indexSquad)}}</span>
    <p class="level">Niveau de compétence: {{squad.level}}</p>
</li>
</template>

<script>
import { alphabetMilitaire, formatTime } from "@/labels";
import { endMission } from "@/missions";
import { state } from "@/game";

export default {
  name: "Squad",
  props: {
    showStatus: { type: Boolean, default: false },
    indexSquad: Number
  },
  data() {
    return {
      t: Date.now()
    };
  },
  computed: {
    squad() {
      return state.squads[this.indexSquad];
    }
  },
  methods: {
    getSquadName: i => alphabetMilitaire[i],
    formatTime: formatTime,
    getSquadMissionTimeLeft(squad) {
      if (!squad.missionEndTime) return "";
      let timeLeft = squad.missionEndTime - this.t;
      if (timeLeft <= 0) {
        endMission(squad);
      }
      return formatTime(timeLeft);
    },

    missionResume() {
      this.$router.push("/mission-resume/" + this.indexSquad);
    }
  },

  created() {
    this.tInterval = setInterval(() => {
      this.t = Date.now();
    }, 1000);
  },

  destroyed() {
    clearInterval(this.tInterval);
  }
};
</script>

