  display: block;
  background-image: url("../assets/squad.png");
  background-size: 100% 100%;
  height: 96px;
  text-align: left;
  overflow: hidden;

  .icon.squad {
    float: left;
    margin: 24px 16px;
  }

  .icon {
    width: 48px;
    height: 48px;
    margin: 8px 16px;
    display: inline-block;
  }

  .name {
    display: block;
    padding-top: 10px;
  }

  .level {
  }

  .status {
    cursor: pointer;
    width: 96px;
    height: 89px;
    float: right;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.25);
    padding: 10px 0;
    margin: 2px 0px;
    line-height: 1.3;
    box-shadow: -4px 0 4px rgba(0, 0, 0, 0.5);

    .time {
      display: block;
      margin: 0.25em 0;
    }

    p {
      padding: 0;
    }
  }

  .status.missionSuccess {
    background-color: rgba(1, 195, 25, 0.25);
  }
  .status.missionFail {
    background-color: rgba(195, 30, 30, 0.25);
  }
  .status.dead {
    background-color: rgba(255, 50, 50, 0.5);
  }
  .status.idle {
    background-color: rgba(195, 195, 25, 0.15);
  }
}
</style>
